.apply {
    width: 100%;
    height: 100vh;
}
.top-title {
    padding: 0 26.1%;
    padding-top: 3.8vh;
    box-sizing: border-box;
}
.content_type {
    width: 100%;
}

.content_type .type_bg {
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 6.25vh;
    background-position: 50% 50%;
    background-size:cover;
}
.content_type .type_bg img {
    width: auto;
    height: 100%;
}

.btn-apply {
    margin-top: 1.61vh;
    padding: 0 14.4%;
    box-sizing: border-box;
    margin-bottom: 3.2vh;
}

li:last-child .btn-apply {
    margin-bottom: 0;
}
li:last-child .type_bg img {
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 30.6%;
    height: auto;
    transform: translateX(-50%);
}
li:nth-child(2) .type_bg img {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 31.8%;
    height: auto;
    transform: translate(-50% , -50%);
}
