.com {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}
.top-com {
    position: absolute;
}
.title-com {
    position: absolute;
    top: 2.08vh;
    left: 0;
    width: 100%;
}
.btn-com {
    position: absolute;
    bottom: 30vh;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    z-index: 10;
}